    getOneInfo () {	// 
			let _info = this.entInfo.entAddStaff

			let _f = false
      _info.forEach(item => {
        if (item.IDNum === this.rRIRDInfo.IDNum) {
					_f = true
          this.rRIRDInfo = item
        }
			});

			if (!_f) {
				this.$message.error('')
				return false
			} else {
				return true
			}
    },
    rowClick (row) {
      this.currentRow = row
    },
    modify (visible, dataForm) {
      if (!this.currentRow) {
        this.$message.info('');
      }
      this.mode = 1  // 1
      this[visible] = true
      this[dataForm] = this.currentRow
    },
    unopenFunction () {
      this.$message.info('');
